body,
html {
  margin: 0;
  padding: 0;
  height: 100vh;
}

::-webkit-scrollbar { 
  display: none; 
  }

.leftPane {
  position: fixed;
  width: 70%;
  max-width: 70%;
  height: 100vh;
}

.rightPane {
  position: absolute;
  right: 0px;
  width: 30%;
  max-width: 30%;
}


.boxAttribute,.boxValue {
  font-size: 14px;
}

.input {
  width: 100%;
  box-sizing: border-box;
  background-color: white;
  z-index: 1
}

.bar {
  top: 0;
  position: sticky;
  background-color: white;
  z-index: 1
}

.result {
  position:relative;
  cursor: pointer;
}